___CSS_LOADER_EXPORT___.push([module.id, `:root, body, button, ul {
    font-family: 'Graphik', 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
    font-size: 1rem;
    line-height: 1.5;
    font-weight: 400;
    padding: 0;
    margin: 0;
}

#content {
    display: flex;
    flex-direction: column;
}

#header {
    display: grid;
    grid-template-rows: 1fr;
    grid-template-columns: 1fr 1fr 1fr;
    align-items: center;
    justify-content: space-between;
    background-color: #f0e7b3;
    padding: 1rem 2rem;
}

#logo {
    font-family: Cambria, Cochin, Georgia, Times, 'Times New Roman', serif;
    font-weight: 600;
    font-size: 1.5rem;
    justify-self: start;
}

#header > ul {
    display: flex;
    flex-direction: row;
    gap: 0.5rem;
    list-style: none;
    justify-self: center;
    margin: 0;
}

#footer {
    display: grid;
    grid-template-columns: 1fr;
    gap: 1rem;
    padding: 1rem 2rem;
    color: #ffffff;
    background-color: #4b4b4b;
}

#footer > ul {
    list-style: none;
    display: flex;
    gap: 2rem;
}

a.footerListLink {
    color: #ffffff;
    text-decoration: none;
    height: 1.5rem;
}

a.footerListLink:hover {
    text-decoration-line: underline;
    text-underline-offset: 2px;
    text-decoration-color: #f0e7b3;
}

#copyright {
    border-top: 1px solid #8f896b;
    padding: 2rem 0rem;
    font-size: 0.875rem;
}

button {
    box-sizing: border-box;
    padding: 0.5rem 1rem;
    justify-self: end;
    border: none;
    border-radius: 0.25rem;
    background-color: #4b4b4b;
    color:#ffffff;
}

button:hover {
    background-color: #1b1b1b;
    cursor: pointer;
    transition: 0.5s;
}

button.tabListButton {
    background-color: #f0e7b3;
    color: #000000;
}

button.tabListButton:hover {
    background-color: #8f896b;
    color:#ffffff;
}

button.tabListButton.active {
    background-color: #8f896b;
    color:#ffffff;
    transition: 0.3s;
}`, "",{"version":3,"sources":["webpack://./src/style.css"],"names":[],"mappings":"AAAA;IACI,uEAAuE;IACvE,eAAe;IACf,gBAAgB;IAChB,gBAAgB;IAChB,UAAU;IACV,SAAS;AACb;;AAEA;IACI,aAAa;IACb,sBAAsB;AAC1B;;AAEA;IACI,aAAa;IACb,uBAAuB;IACvB,kCAAkC;IAClC,mBAAmB;IACnB,8BAA8B;IAC9B,yBAAyB;IACzB,kBAAkB;AACtB;;AAEA;IACI,sEAAsE;IACtE,gBAAgB;IAChB,iBAAiB;IACjB,mBAAmB;AACvB;;AAEA;IACI,aAAa;IACb,mBAAmB;IACnB,WAAW;IACX,gBAAgB;IAChB,oBAAoB;IACpB,SAAS;AACb;;AAEA;IACI,aAAa;IACb,0BAA0B;IAC1B,SAAS;IACT,kBAAkB;IAClB,cAAc;IACd,yBAAyB;AAC7B;;AAEA;IACI,gBAAgB;IAChB,aAAa;IACb,SAAS;AACb;;AAEA;IACI,cAAc;IACd,qBAAqB;IACrB,cAAc;AAClB;;AAEA;IACI,+BAA+B;IAC/B,0BAA0B;IAC1B,8BAA8B;AAClC;;AAEA;IACI,6BAA6B;IAC7B,kBAAkB;IAClB,mBAAmB;AACvB;;AAEA;IACI,sBAAsB;IACtB,oBAAoB;IACpB,iBAAiB;IACjB,YAAY;IACZ,sBAAsB;IACtB,yBAAyB;IACzB,aAAa;AACjB;;AAEA;IACI,yBAAyB;IACzB,eAAe;IACf,gBAAgB;AACpB;;AAEA;IACI,yBAAyB;IACzB,cAAc;AAClB;;AAEA;IACI,yBAAyB;IACzB,aAAa;AACjB;;AAEA;IACI,yBAAyB;IACzB,aAAa;IACb,gBAAgB;AACpB","sourcesContent":[":root, body, button, ul {\n    font-family: 'Graphik', 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;\n    font-size: 1rem;\n    line-height: 1.5;\n    font-weight: 400;\n    padding: 0;\n    margin: 0;\n}\n\n#content {\n    display: flex;\n    flex-direction: column;\n}\n\n#header {\n    display: grid;\n    grid-template-rows: 1fr;\n    grid-template-columns: 1fr 1fr 1fr;\n    align-items: center;\n    justify-content: space-between;\n    background-color: #f0e7b3;\n    padding: 1rem 2rem;\n}\n\n#logo {\n    font-family: Cambria, Cochin, Georgia, Times, 'Times New Roman', serif;\n    font-weight: 600;\n    font-size: 1.5rem;\n    justify-self: start;\n}\n\n#header > ul {\n    display: flex;\n    flex-direction: row;\n    gap: 0.5rem;\n    list-style: none;\n    justify-self: center;\n    margin: 0;\n}\n\n#footer {\n    display: grid;\n    grid-template-columns: 1fr;\n    gap: 1rem;\n    padding: 1rem 2rem;\n    color: #ffffff;\n    background-color: #4b4b4b;\n}\n\n#footer > ul {\n    list-style